img{
    width: 400px;
    height: 400px;
}
img:hover{
    transform: scale(1.1);
    transition: 2s;
}
.container{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.count{
    justify-content: space-between;
    flex-direction: column;
}
h2{
    font-size: 40px;
    color: chocolate;
    font-style: italic;
    margin-left: 70px
}
span{
    color: brown;
    font-size: 20px;
}
.btn{
    background-color: burlywood;
    width: 70px;
    height: 30px;
    margin-left: 180px;
}
p{
    font-size: 20px;
    font-weight: 600;
    margin-left: 210px;
}
h3{
    color: maroon;
    font-size: 30px;
}
.set{
    cursor: pointer;
    margin: 0;
}
.App{
    display: flex;
    justify-content: space-between;
}